.paperBook
{
    background-color: #f1f1f1;
    overflow-y: auto;
    overflow-x: hidden;
    overflow-wrap: normal;
    top: 1%;
    left: 0%;
    width: 95%;
    height: 98%;
    -webkit-transform-style: flat;
            transform-style: flat;
    box-shadow: -1em 2px 3px 2px #1f120f;
    border-left: 2px outset #b3b3b3;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;

    display: flex;
    flex-direction: column;
    align-items: center;
}

/*Make sure you import flip.css*/
.flipForward
{
    -webkit-animation-name: flip;
            animation-name: flip;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-transform-origin: left;
            transform-origin: left;
}

.flipForwardPage
{
    -webkit-animation-name: flipPage;
            animation-name: flipPage;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-transform-origin: left;
            transform-origin: left;
    box-shadow: -2em 5px 5px 3px #1f120f;
}

.flipBack
{
    -webkit-animation-name: unflip;
            animation-name: unflip;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-transform-origin: left;
            transform-origin: left;
}

