.inputCluster 
{
    position: relative;
    display: flex;
    width: 90%;
    margin-left: 5%;
    margin-right: 5%;
    margin-top: 4%;
}

.inputCluster input
{
    width: 100%;
    font: lighter 14pt 'Comfortaa';
    border-radius: 2em;
    color:rgb(161, 161, 161);
    border: 1px solid rgb(153, 153, 153);
    background: white;
    transition: border .3s ease;
    line-height: 25px;
    padding: 6px 14px;
    display: block;
    position: relative;
    flex: 1 1 auto;
    width: 1%;
    margin-top: 0%;
    margin-bottom: 0%;
}

.inputCluster select, .inputCluster .selectPut
{
    width: 100%;
    font: lighter 12pt 'Comfortaa';
    color:rgb(161, 161, 161);
    border: 1px solid rgb(153, 153, 153);
    background: white;
    transition: border .3s ease;
    line-height: 25px;
    padding: 6px 14px;
    border-top-right-radius: 1.5em;
    border-bottom-right-radius: 1.5em;
    display: block;
    position: relative;
    flex: 1 1 auto;
    width: 1%;
    margin-top: 0%;
    margin-bottom: 0%;
}

.inputTextFocus
{
    outline: none;
    border: 2px solid #ffa260 !important;
    color: black !important;
}

.inputSpanFocus
{
    color: white !important;
    background-color: #ffa260 !important;
}

.inputCluster input::-webkit-input-placeholder
{
    color: rgb(161, 161, 161);
}

.inputCluster input:-ms-input-placeholder
{
    color: rgb(161, 161, 161);
}

.inputCluster input::placeholder
{
    color: rgb(161, 161, 161);
}

.inputCluster > span
{
    background: white;
    border: 1px solid rgb(153, 153, 153);
    text-align: center;
    padding: 8px 12px;
    font: 14pt 'Comfortaa';
    line-height: 25px;
    color: rgb(161, 161, 161);
    transition: background .3s ease, border .3s ease, color .3s ease;
}

.inputCluster input, .inputCluster > span
{
    display: block;
    white-space: nowrap;
}

.inputCluster input:not(:first-child):not(:last-child), .inputCluster > span:not(:first-child):not(:last-child)
{
    border-radius: 0px;
}

/*May not work w/ responsive design, idk*/
.inputCluster input:first-child, .inputCluster > span:first-child
{
    border-radius: 5px 1px 1px 5px;
}

.inputCluster input:last-child, .inputCluster > span:last-child
{
    border-radius: 0px 5px 5px 0px;
}

.inputCluster input:not(:first-child), .inputCluster > span:not(:first-child)
{
    margin-left: -1px;
}

.flip 
{
    display: inline-block;
    width: 25%;
    height:100%;
    padding-top: 15px;
    text-align: center;
    background-color:rgb(160, 247, 0);
}

.flip::after 
{
    content: "";
    position: static;
    left: 0;
    bottom: 0;
    border-bottom: 2.8em solid #f1f1f1;
    border-left: 2.4em solid transparent;
    border-right: 2.5em solid transparent;
    cursor: pointer;
}

