@-webkit-keyframes flip
{
    from {-webkit-transform: rotateY(0);transform: rotateY(0)}
    to {-webkit-transform: rotateY(-120deg);transform: rotateY(-120deg)}
}

@keyframes flip
{
    from {-webkit-transform: rotateY(0);transform: rotateY(0)}
    to {-webkit-transform: rotateY(-120deg);transform: rotateY(-120deg)}
}

@-webkit-keyframes flipPage
{
    from {-webkit-transform: rotateY(0);transform: rotateY(0); border-right: 2px solid grey;}
    to {-webkit-transform: rotateY(-118deg);transform: rotateY(-118deg); border-right: none;}
}

@keyframes flipPage
{
    from {-webkit-transform: rotateY(0);transform: rotateY(0); border-right: 2px solid grey;}
    to {-webkit-transform: rotateY(-118deg);transform: rotateY(-118deg); border-right: none;}
}

@-webkit-keyframes unflip
{
    from {-webkit-transform: rotateY(-120deg);transform: rotateY(-120deg); border-right: 2px solid grey;}
    to {-webkit-transform: rotateY(0);transform: rotateY(0); border-right: none;}
}

@keyframes unflip
{
    from {-webkit-transform: rotateY(-120deg);transform: rotateY(-120deg); border-right: 2px solid grey;}
    to {-webkit-transform: rotateY(0);transform: rotateY(0); border-right: none;}
}
@-webkit-keyframes HideList {
    from {
        -webkit-transform: scaleY(1);
                transform: scaleY(1);
    }
    to {
        -webkit-transform: scaleY(0);
                transform: scaleY(0);
    }
}
@keyframes HideList {
    from {
        -webkit-transform: scaleY(1);
                transform: scaleY(1);
    }
    to {
        -webkit-transform: scaleY(0);
                transform: scaleY(0);
    }
}
